.banner {
  width: 100%;
  height: max(400px, 60vh);
  position: relative;
  overflow: hidden;
  transition: height 1s ease-in-out;
}
.banner:hover {
  height: max(450px, 75vh);
}
.imageContainer {
  display: flex;

  align-items: center;
  height: 100%;
}
.imageContainer img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.info {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  width: max(400px, 35%);
  position: absolute;
  top: 22vh;
  left: 30px;
  padding: 20px;
  border-radius: 10px;
}
.info h1 {
  margin-bottom: 40px;
}
.info p {
  font-size: 12px;
  max-height: 300px;
  overflow-y: scroll;
}

.info button {
  border: none;

  font-size: 14px;
  font-weight: 500;
  color: white;
  background-color: rgba(0, 0, 0, 0.737);
  padding: 8px 24px;
  margin-right: 10px;
  cursor: pointer;
}
