.movieDetail {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  gap: 20px;
}

/* .movieDetail > * {
  flex-basis: 48%;
 
} */
.movieInfo {
  width: max(45%, 400px);
  margin-inline: auto;
}
.title {
  font-size: 24px;
  margin-block: 20px;
}
.releaseDate,
.rating {
  font-weight: bold;
}

.description {
  padding: 20px 0;
  font-size: 14px;
}
.movieTrailer {
  width: max(50%, 400px);
  margin-inline: auto;
}
.movieTrailer img {
  width: 100%;
}
