.formContainer {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 30vh;
  margin-bottom: 30px;
}

.inputs {
  display: flex;
  height: 50px;
  width: 40vw;
  background-color: white;
  padding: 12px 16px;
  border-bottom: 3px solid rgb(84, 187, 252);
}
.inputs input {
  font-size: 1rem;
  width: 100%;
  border: none;
  outline: none;
}

.inputs svg {
  fill: gray;
}

.actions {
  background-color: #fff;
  height: 100px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 20px;
}

.actions button {
  border: none;
  outline: none;
  padding: 10px 10px;
  background-color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
  margin-right: 20px;
}

.actions .btnSearch {
  background-color: rgb(84, 187, 252);
  color: white;
}
