.moviesList {
  display: flex;
  height: fit-content;
  justify-items: center;
  align-items: center;
  overflow-x: scroll;
  margin-bottom: 50px;
}

.moviesList::-webkit-scrollbar {
  height: 6px;
}
.moviesList::-webkit-scrollbar-track-piece {
  background: #f1f3f5;
}
.moviesList::-webkit-scrollbar-thumb {
  background: #868e96;
}

.movieItem {
  /* height: 30%; */
  margin: 20px 10px;
}
.movieItem img {
  height: 100%;
  transition: transform 0.3s ease-in-out;
}
.backdrop img {
  width: 250px;
}
.poster img {
  width: 150px;
}
.movieItem img:hover {
  transform: scale(1.1);
}
