.NavBar {
  position: fixed;
  display: flex;
  background-color: transparent;
  /* justify-content: center; */
  align-items: center;

  width: 100%;

  height: 50px;

  padding-inline: 12vw;
  z-index: 20;

  transition: background-color 0.5s ease-in-out;
}

.NavBar h2 {
  color: red;
  cursor: pointer;
  margin-right: auto;
}

.NavBar svg {
  height: 30px;
  max-height: 100%;

  cursor: pointer;

  border-radius: 50%;
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.5);
}

.NavBar.bgDark {
  background-color: black;
}
